// Call plugin.
import * as sagas from './sagas'
import anonymousAPI from '../interfaceNew/api/callMe'

// Other plugins
import callsNewLink from '../link'
import { logManager } from '../../logs'

// Helpers.
import { autoRestart, mergeValues } from '../../common/utils'

// Libraries.
import { values } from 'lodash/fp'
import { fork } from 'redux-saga/effects'
import { getWebRTCSupportCapabilities } from 'kandy-webrtc'

// Parse and/or Validate
import { validation as v8n, parse, enums } from '../../../src/common/validation'

const log = logManager.getLogger('CALLME')

const defaultOptions = {
  sdpSemantics: 'unified-plan',
  iceServers: [],
  iceCollectionDelay: 1000,
  maxIceTimeout: 3000,
  // Defaults set by the Webrtc stack:
  //    iceCollectionCheck: Has at least one relay candidate.
  serverTurnCredentials: true,
  sdpHandlers: [],
  removeH264Codecs: true,
  earlyMedia: false,
  resyncOnConnect: false,
  mediaBrokerOnly: false,
  callAuditTimer: 25000,
  mediaConnectionRetryDelay: 3000
}

// config validation
const v8nValidation = v8n.schema({
  sdpSemantics: enums(['unified-plan', 'plan-b']),
  iceServers: v8n.array(),
  iceCollectionDelay: v8n.positive(),
  maxIceTimeout: v8n.positive(),
  // Defaults set by the Webrtc stack:
  //     iceCollectionCheck: v8n.optional(v8n.function()),
  serverTurnCredentials: v8n.boolean(),
  sdpHandlers: v8n.array(),
  removeH264Codecs: v8n.boolean(),
  earlyMedia: v8n.boolean(),
  resyncOnConnect: v8n.boolean(),
  mediaBrokerOnly: v8n.boolean(),
  callAuditTimer: v8n.positive(),
  mediaConnectionRetryDelay: v8n.positive()
})
const parseOptions = parse('call', v8nValidation)

export default function newCallMePlugin (options = {}) {
  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
  if (!mediaDevices || !peerConnection) {
    log.warn(
      'Calls are not supported on this platform due to lack of WebRTC support. CallMe APIs will not be available.'
    )
    return
  }

  const linkCallPlugin = callsNewLink(options)

  return {
    name: linkCallPlugin.name,
    api: anonymousAPI(linkCallPlugin.api),
    reducer: linkCallPlugin.reducer,
    init: anonymousInit(linkCallPlugin.init),
    capabilities: ['callMe']
  }
}

/**
 * Returns an init function which also calls another plugin's init function.
 * @method anonymousInit
 * @param {function} otherInit The other plugin's init function.
 */
function anonymousInit (otherInit) {
  return function * init ({ webRTC }) {
    // Run the other plugin's init
    yield fork(otherInit, { webRTC })

    // Wrap the newCallMe sagas in a function that provides them with the webRTC stack.
    const deps = { webRTC, browserDetails: webRTC.getBrowserDetails }
    const wrappedSagas = values(sagas).map(saga => {
      return autoRestart(() => saga(deps))
    })

    // Run all of the sagas.
    for (const saga of wrappedSagas) {
      yield fork(saga)
    }
  }
}
